"use server";

import { auth } from "@/auth";
import { prisma } from "@/src/lib/prisma";
import bcrypt from "bcryptjs";
import { revalidatePath } from "next/cache";

export async function updateProfile(formData: FormData) {
    const session = await auth();
    if (!session?.user?.id) {
        return { error: "„É≠„Ç∞„Ç§„É≥„Åó„Å¶„Åè„Å†„Åï„ÅÑ" };
    }

    const email = formData.get("email") as string;
    const password = formData.get("password") as string;

    if (!email && !password) {
        return { error: "Â§âÊõ¥ÂÜÖÂÆπ„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ" };
    }

    const updateData: any = {};

    if (email) {
        // Check if email is already taken by another user
        const existingUser = await prisma.user.findUnique({
            where: { email },
        });
        if (existingUser && existingUser.id !== session.user.id) {
            return { error: "„Åì„ÅÆ„É°„Éº„É´„Ç¢„Éâ„É¨„Çπ„ÅØÊó¢„Å´‰ΩøÁî®„Åï„Çå„Å¶„ÅÑ„Åæ„Åô" };
        }
        updateData.email = email;
    }

    if (password) {
        if (password.length < 6) {
            return { error: "„Éë„Çπ„ÉØ„Éº„Éâ„ÅØ6ÊñáÂ≠ó‰ª•‰∏ä„ÅßÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ" };
        }
        const hashedPassword = await bcrypt.hash(password, 10);
        updateData.password = hashedPassword;
    }

    try {
        await prisma.user.update({
            where: { id: session.user.id },
            data: updateData,
        });
        revalidatePath("/profile");
        return { success: "„Éó„É≠„Éï„Ç£„Éº„É´„ÇíÊõ¥Êñ∞„Åó„Åæ„Åó„Åü" };
    } catch (error) {
        console.error("Profile update error:", error);
        return { error: "„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü" };
    }
}
